form.Seach {
    max-width: 500px;
    max-height: 100px;
  }
  .seach__input {
    width: 350px;
    margin: 50px 0px 20px 50px;
    padding: 10px 0 10px 20px;
    border-radius: 25px;
    border: 2px solid rgb(117, 79, 7);
    background: rgb(243, 216, 140);
    color: rgb(124, 119, 119);
  }
  .main__label {
    width: 70px;
    height: 40px;
    margin-left: 10px;
    border-radius: 20px;
    background: rgba(240, 214, 169, 0.425);
    border: 2px solid rgb(117, 79, 7);
    text-transform: uppercase;
  }
  .main__label:hover {
    box-shadow: 5px 5px 5px 0px rgba(117, 79, 7, 0.582);
    transition: all ease 0.3s;
  }
  

.weather {
    margin-top: 50px;
  }
  .main__place {
    max-width: 700px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .main__place-img {
    max-width: 350px;
    display: flex;
    align-items: center;
  }
  .main__place-deg {
    max-width: 110px;
    font-weight: 600;
    font-size: 35px;
    margin-left: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .main__place-about {
    max-width: 400px;
    font-size: 16px;
  }
  .deg-cel {
    font-size: 18px;
    font-weight: 300;
    text-decoration: none;
    color: rgb(51, 48, 48);
  }
  
  .deg-cel:hover {
    font-weight: 600;
    color: rgb(51, 48, 48);
  }
  .main__place-place {
    max-width: 200px;
    font-weight: 600;
    font-size: 40px;
    margin-left: 15px;
  }
  .main__time {
    max-width: 1400px;
    margin: 50px auto;
    display: flex;
  }
  .main__time-title {
    margin-left: 100px;
    font-weight: 500;
  }

  @media(max-width: 498px) {
    .weather {
      margin-top: 50px;
    }
    .main__place {
      max-width: 450px;
      padding: 0 15px;
      display: block;
      margin-left: 50px;
    }
    .main__place-deg {
      margin-left: 50px;    
  }
  .main__label {
    margin-left: 50px; 
}
}