.main__clock {
  margin-left: 50px;
}
.main__date {
    max-width: 700px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 40px auto 10px;
    border-radius: 15px;
    box-shadow: 3px 3px 3px 1px rgba(117, 79, 7, 0.582);
  }
  .main__date-day {
    font-weight: 500;
  }
  .main__date-item {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
  }
  
  .item {
    width: 110px;
    text-align: center;
    display: inline-table;
  }