body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@import url(
  https://fonts.googleapis.com/css?family=Poppins:100,200,300,regular,500,600,600italic,700,700italic,800,900);
li {
  list-style: none;
}
a {
  text-decoration: none;
}

.App {
  max-width: 1300px;
  height: 700px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper {
  font-family: "Poppins", sans-serif;
  width: 800px;
  height: 500px;
  margin: 0 auto;
  color: rgb(51, 48, 48);
  background: linear-gradient(
    135deg,
    rgb(243, 216, 140) 26%,
    rgb(168, 129, 28)
  );
  box-shadow: 8px 8px 10px 0px rgba(94, 93, 3, 0.322);
  border-radius: 15px;
}



@media(max-width: 699px) {
  .wrapper {
    max-width: 600px;
    
  }
}
@media(max-width: 498px) {
  .wrapper {
    max-width: 450px;
    
  }
}

